body {
    font: 1.6rem Helvetica;
    margin: 0;
    position: relative;
    min-height: 100vh;
    padding: 0;
}


.pickupContainer {
    background-color: black;
}

.grid-container { 
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-bottom: 2.5rem;
    padding-top: 0;
}

.pickupHeader { 
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
}

.brand { 
    margin-left: 10%;
    width: 25%;
}

.brand a {
    color: white;
    text-decoration: none;
    font-size: 5vw;
    font-weight: bold;
    font-family: 'Dancing Script', cursive;
}

.header-links { 
    font-size: 2vw;
    display: flex;
    flex-direction: row;
}

.link-text {
    color: white;
    padding: 1%;
    text-decoration: none;
}

.link-text:hover {
    color: #2F2E2E;
    text-decoration: none;
}

.shop {
	width: 100vw;
	height: 100vh;
	background-color: white;
}

.menuCategories {
	width: 90%;
	height: 8%;
	display: flex;
	flex-direction: row;
	align-items: center;
	padding-left: 10%;
	border-bottom: .2rem solid black;
	background-color: #DCDCDC;
}

.dailyMenu {
	flex: 1;
	white-space: nowrap;
}

.break{
    width: .3rem;
    height: 80%;
    background-color: black;
   	margin-left: 1%;
   	margin-right: 1%;
}

.productLinks {
	display: flex;
	flex-direction: row;
	align-items: center;
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
}

.category {
	font-size: 1.3rem;
	margin-left: 1%;
	margin-right: 1%;
	flex: 0;
	text-decoration: none;
	color: black;
	white-space: nowrap;
}

.category:hover {
	text-decoration: underline;
}

.category:active {
	color: grey;
}

.cartObject {
	color: black;
	font-size: 1.3rem;
	margin-right: 5%;
}

.mainMenu {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: row;
	background-color: blue;
	justify-content: flex-start;
	align-items: flex-start;
}

.filters {
	flex: 1;
	background-color: red;
	text-align: center;
	display: inline-block;
	height: 100%;
}

.menuItems {
	flex: 4;
	display: inline-block;
	flex-direction: column;
	background-color: green;
	justify-content: flex-start;
	padding-left: 5%;
	padding-right: 5%;
}

.itemTitle {
	margin-top: 4%;
	font-size: 20pt;
}

.singleItem {
	display: flex;
	flex: 1;
	height: 5rem;
	margin-top: 2%;
	margin-bottom: 2%;
	width: 100%;
	font-size: 10pt;
	font-weight: bold;
	color: white;
	text-decoration: none;
	font-family: 'Roboto', sans-serif;
	background-color: white;
	border: .1rem solid black;
}

.itemPicture {
	height: 100%;
	width: 15%;
	border-right: .1rem solid black;
}








