.App {
    text-align: center;
    font-size: 63.5%; /* 16px * 62.5% = 10px = 1rem*/
    box-sizing: border-box;
}

body {
    font: 1.6rem Helvetica;
    margin: 0;
    position: relative;
    min-height: 100%;
    padding: 0;
}

.homepageContainer{
    background-image: url('../images/cannabis.jpg');
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.grid-container { 
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-bottom: 2.5rem;
}

.landing{
    height: 100vh;
    width: 100%;
}

.header { 
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    top: 0;
}

.brand { 
    margin-left: 10%;
    width: 25%;
}
.brand a {
    color: white;
    text-decoration: none;
    font-size: 3rem;
    font-weight: bold;
    font-family: 'Dancing Script', cursive;
}

.header-links { 
    margin-right: 5%;
    font-size: 1.5vw;
    width: 40%;
    display: inline-flex;
    flex-direction: row;
}

.link-text{
    color: white;
    padding: 1%;
    text-decoration: none;
}

.link-text:hover{
    color: #2F2E2E;
    text-decoration: none;
}

.main { 
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow: hidden;
    height: 100%;
    width: 100%;
    margin-top: 10%;
}

.landing-info { 
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.phrase {  
  color: white;
  width: 35%;
  font-size: 5rem;
  font-weight: bold;
  margin-left: 10%;
  text-align: center
}

.landing-description {
  color: white;
  font-size: 2rem;
  width: 30%;
  margin-right: 10%;
}

.icon { 
  margin-top: 5%;
  font-size: 60pt;
  display: flex;
  justify-content: center;
}

.icon a{
  color: white;
}

.icon a:hover{
  color: #2F2E2E;
}

.product_banners{
  width: 95vw;
  height: 100%;
  padding-left: 2.5%;
  padding-right: 2.5%;
  padding-top: 2.5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
}

.product { 
  flex: 1;
  padding-top: 7%;
  padding-bottom: 7%;
  margin-top: 2%;
  margin-bottom: 2%;
  width: 100%;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: 50pt;
  font-weight: bold;
  color: white;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  -webkit-filter: brightness(100%);
}

.product:hover{
    -webkit-filter: brightness(40%);
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -o-transition: all .5s ease;
    -ms-transition: all .5s ease;
    transition: all .5s ease;
}

#flower{
    background-image: url('../images/flower.jpg');
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center;
}

#edible{
    background-image: url('../images/edible.jpg');
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center;
}

#concentrate {
    background-image: url('../images/concentrate.jpg');
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center;
}

#pre-roll {
    background-image: url('../images/preroll.jpg');
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center;
}

#vaporizer {
    background-image: url('../images/vaporizer.jpg');
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center;
}

#cbd {
    background-image: url('../images/cbd.jpg');
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center;
}

#about { 
    width: 100%;
    height: 100%;
    padding-bottom: 5%;
    background-color: #2F2E2E;
    display: flex;
    text-align: center;
    flex-direction: column;
    color: white;
}

.profiles{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.employee{
    flex: 1;
    display: flex;
    flex-direction: column;
    width: 20%;
    height: 100%;
    text-align: center;
    align-items: center;
}
.profile-picture{
    width: 80%;
    padding-top: 40%;
    padding-bottom: 40%;
    border-radius: 100%;
    margin-bottom: 10%;
    border: 1px solid white;
    background-image: url('../images/profile.jpeg');
    background-repeat:no-repeat;
    background-size: cover;
    background-position: top;
}

.profile-name {
    margin-bottom: 10%;
    font-size: 1.8rem;
    font-style: italic;
}

.position { 
    font-size: 1.4rem;
}

#contact { 
    width: 100%;
    background-color: white;
    display: flex;
    color: #2F2E2E;
    display: flex;
    flex-direction: column;
}

#contact-upper-half{
    text-align: center;
    display: flex;
    flex-direction: column;
    height: 40%;
    width: 100%;
}

.contact-methods{ 
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: row;
    height: 100%;
    width: 100%;
}

.method{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    margin-left: 2%;
    margin-right: 2%;
    font-size: 14pt;
    font-weight: bold;
}

.divider{
    width: .5rem;
    height: 50%;
    background-color: #2F2E2E;
}

#contact-lower-half{
    text-align: center;
    display: flex;
    flex-direction: column;
    height: 60%;
    width: 100%;
    
}

.map{
    width: 100%;
    height: 60vh;
}

.footer { 
    position: absolute;
    bottom: 0;
    margin-top: 10%;
    height: 4.3rem;
    width: 100%;
    background-color: #2F2E2E;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}


/*@media (min-width: 1301px) {
    .header{
        margin-top: 5%;
    }
}*/

@media (max-width: 1300px) {
    .header{ 
        align-items: center;
    }
    .main{
        margin-top: 3%;
    }
    .phrase{
        font-size: 4rem;
    }
    .icon{
        margin-top: 2%;
    }
}